'use scrict';
import { SUCCESSPAY } from '@/config/image.js';
import { getCheckIn, checkIn } from '@/api/tabbar/mine.js';
import { getStorage } from '@/utils/storage.js';
import { loanListDetail } from '@/api/todoChild/loan.js'

var _self;

export default {
	data() {
		return {
			orderList:[],
			periodList:[],
			img: SUCCESSPAY,
			latitude: 22.686206,
			longitude: 114.230672,
			covers: [
				{
					id: 1,
					latitude: 22.686206,
					longitude: 114.230672,
					label: {
						content: '爱智慧有限公司',
						color: '#ddd',
						borderRadius: 12,
						bgColor: '#000',
						padding: 6,
						display: 'ALWAYS',
						textAlign: 'center'
					},
					iconPath: '../../../static/location.png'
				}
			],
			
			userInfo: {},
			
			tempFilePaths: '',
			huadongs: [
				{ img: 'tempFilePaths', name: '舟舟', info: 'xxxxxxxx' },
				{ img: 'tempFilePaths', name: '舟舟', info: 'xxxxxxxx' },
				{ img: 'tempFilePaths', name: '舟舟', info: 'xxxxxxxx' },
				{ img: 'tempFilePaths', name: '舟舟', info: 'xxxxxxxx' },
				{ img: 'tempFilePaths', name: '舟舟', info: 'xxxxxxxx' },
				{ img: 'tempFilePaths', name: '舟舟', info: 'xxxxxxxx' }
			],
			
		};
	},
	filters:{
		num(val){
			return Number(val).toFixed(2)
		},
		arry(val){
			let b=["待预审" ,"预审通过（待面签）","预审没过", "面签通过", "面签未通过"]
			let e=Number(val) - 1
			return b[e]
		},
		time(val){
			var time = new Date(val);
			    
			      function timeAdd0(str) {
			        if (str < 10) {
			          str = '0' + str;
			        }
			        return str
			      }
			      var y = time.getFullYear();
			      var m = time.getMonth() + 1;
			      var d = time.getDate();
			      var h = time.getHours();
			      var mm = time.getMinutes();
			      var s = time.getSeconds();
			      return y + '-' + timeAdd0(m) + '-' + timeAdd0(d) + ' ' + timeAdd0(h) + ':' + timeAdd0(mm) + ':' + timeAdd0(s);
		}
	},
	methods: {
		
		// 选择照片
		chooseimage: function() {
			var _this = this;
			uni.chooseImage({
				count: 9, // 默认9
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
					_self.tempFilePaths = res.tempFilePaths;
				}
			});
		},
		
	},
	onLoad: async function(options) {
		this.userInfo = getStorage('userInfo');
		console.log(this.userInfo);
		_self = this;
		console.log(options)
		let v = await loanListDetail({orderid:options.id})
		console.log(v)
		_self.orderList =v.order[0]
		_self.periodList =v.period[0]
		
	},
	components: {
		// ApplicationInput
		
	}
};
